<template>
    <div class="detail" ref="detailRef">
        <loading v-if="loading"></loading>
        <note
                v-else
                ref="noteRef"
                :des="blog.des"
                :author="blog.author"
                :avatar="blog.avatar"
                :time="blog.time"
                :detail="blog.detail"
        ></note>
    </div>
</template>

<script>
  import Note from '../components/Note'
  import Loading from '../components/Loading'

  export default {
    name: 'detail',
    components: {
      'note': Note,
      'loading': Loading
    },
    data() {
      return {
        blog: {},
        loading: false
      }
    },
    methods: {
      getBlog() {
        this.loading = true;
        this.$ajax.get(`https://api.github.com/gists/${this.$route.query.id}`).then((res) => {
          this.blog.des = res.data.description
          this.blog.author = res.data.owner.login
          this.blog.avatar = res.data.owner.avatar_url
          this.blog.time = res.data.created_at
          this.blog.detail = res.data.files.blog.content
          this.loading = false;
          this.$nextTick(function () {
            this.$refs.detailRef.scrollTop = this.$store.state.scrollTops[this.$route.query.id]
          })
        })
      }
    },
    activated() {
      this.getBlog()
    },
    //记住滚动位置
    beforeRouteLeave(to, from, next) {
      this.$store.commit('SET_SCROLLTOPS', {
        key: this.$route.query.id,
        value: this.$refs.detailRef.scrollTop
      });
      next();
    },
  }
</script>


<style lang="scss" scoped>
    .detail {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>